.page_result {
  padding: 0;

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: .8em .75em 5.5em;
    flex-grow: 1;
    background: linear-gradient(167.95deg, #6C23C8 -64.75%, #0D40C5 -64.73%, #EF59B3 120.73%);
  }

  &__emotion {
    margin-top: 1.5em;
    font-size: 3.2em;
  }

  &__text {
    max-width: 200px;
    margin: 1em auto 0;
    text-align: center;
    line-height: 1.3;
  }

  &__body {
    padding: .5em .9em .2em;
    color: #9A9AA0;
  }

  &__charts {
    display: flex;
  }

  &__chart {
    width: calc(100%/3);
    padding: 0 .25em;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: .55em;
      height: .55em;
      border: .05em solid #C3B4B4;
      border-radius: 50%;
    }
    &_active {
      &:before {
        border-color: #BA85CE;
      }
      &:after {
        content: '';
        position: absolute;
        top: .15em;
        left: .15em;
        width: .25em;
        height: .25em;
        background: #BA85CE;
        border-radius: 50%;
      }
    }
  }

  .chart {
    width: 100%;
    max-width: 87px;
    margin: 0 auto;
  }

}